.nav {
	&__list {
		display:flex;
		align-self: center;

		@include tablet {
			flex-direction: column;
		}
	}

	&__item {
&:not(:last-child) {
	margin-right: var(--main-offset);
}

	@include tablet {
		margin-right: 0;
		margin-bottom: var(--small-offset);
	}

	}
	
	&__link {
		position: relative;
		font: var(--nav-font);
		outline: none;
		color: var(--color-white);
		transition: opacity .3s ease-in-out;
		cursor: pointer;
		
		&::before {
			content: "";
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background-color: currentColor;
			transform: scaleX(0);
			transform-origin: center;
			transition: transform .3s ease-in-out;
		}

		&:focus {
			&::before {
				transform: scaleX(1);
			}
		}

		@include desktop {
			&:hover {
				&::before {
					transform: scaleX(1);
				}
			}
		}
		&:active {
			opacity: .7;
		}

		@include tablet {
			font: var(--title-font);
		}
	}
}