.cart{
	position: relative;
	margin-left: var(--main-offset);
	&__btn {
		position: relative;
		width: 20px;
		height: 20px;

		&--inactive {
			pointer-events: none;
		}
		svg{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			fill: var(--color-white);
			pointer-events: none;
			transition: fill .3s ease-in-out;
		}
		&:focus {
			svg {
				fill: var(--color-accent);
			}
		}
		
		@include desktop {
			&:hover {
				svg {
					fill: var(--color-accent);
				}
			}
		}
			
		&:active {
			svg {
				fill: var(--color-accent-hover);
			}
		}
	}
	&__count {
		position: absolute;
		background-color: var(--color-accent);
		right: -6px;
		bottom: -6px;
		height: 16px;
		min-width: 16px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		font: var(--small-font);
		color: var(--color-white);
		opacity: 0;
		transition: opacity .3s ease-in-out;
		pointer-events: none;

		&--visible {
			opacity: 1;
			transition: opacity .3s ease-in-out;
		}
	}
}

.mini-cart {
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
	position: absolute;
	right: 0;
	top: calc(100% + 22px);
	width: 480px;
	box-shadow: 0 -4px 10px rgba(0, 13, 84, 0.1);
	background-color: var(--color-white);

	&__top {
		max-height: 323px;

		.simplebar-track.simplebar-vertical {
			right: 5px;
			width: 4px;
			top: 20px;
			bottom: 20px;
			background: rgba(77, 77, 77, 0.1);
			border-radius: 5px;
		}

		.simplebar-scrollbar::before {
			opacity: 1;
			left: 0;
			right: auto;
			width: 4px;
			background: rgba(77, 77, 77, 0.2);
			border-radius: 5px;

		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: var(--second-offset);
		box-shadow: 0px -4px 10px rgba(0, 13, 84, 0.1);
		background-color: var(--color-white);

		@include mobile {
			flex-wrap: wrap;
		}
	}

	&__txt {
		display: block;
		margin-bottom: var(--small-offset);
	}

	&__sum {
		font: var(--price-font);
		color: #4d4d4d;
	}

	&__btn {
		padding-top: 14px;
		padding-bottom: 14px;


		@include mobile {
			width: 100%;
		}
	}

	&--visible {
	opacity: 1;
	visibility: visible;
	transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
	}

	&__itog {
		@include mobile {
			margin-bottom: 20px;
		}
	}

	@include mobile {
		width: calc(100vw - 30px);
	}
}
