@import "../mixins";

.header{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
	background-color: var(--color-eastbay);


	&__container {
		--paddings: 15px;

		position: relative;
		align-items: center;
		padding: var(--second-offset) var(--paddings);
		
		&::before {
			content: "";
			position: absolute;
			left: 50%;
			bottom: 0;
			width: calc(100% - (var(--paddings) * 2));
			transform: translateX(-50%);
			height: 1px;
			background-color: var(--color-border);
			z-index: 5000;
		}
	}


	&__left {
		grid-column: 2 span;
	}
	&__right {
		grid-column: 10 span;
		display: flex;
		justify-content: flex-end;

		@include mobile {
			grid-column: 2 span;
		}

		@include tablet {
			align-items: center;
		}
	}

	&__nav {
		@include tablet {
			position: fixed;
			left: 0;
			top: 55px;
			z-index: 1000;
			padding: 50px;
			height: 350px;
			width: 100vw;
			background-color: var(--color-eastbay);
			display: none;
			box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.2);

			&--active {
				display: block;
			}
		}
	}
}

.burger {
	display: none;
	@include burger;
	--burger-width: 24px;
	--burger-height: 18px;
	--burger-line-height: 2px;
	color: var(--color-white);

	@include tablet {
		display: block; 
	}
}