@import "../mixins";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200&display=swap');

.footer{
	padding-top: var(--small-offset);
	left: 30px; bottom: 50px;
	color:rgb(255, 255, 255);
	background-color: #494283ea;
	box-shadow: 0px -5px 8px 0px rgba(34, 60, 80, 0.2);
	
	@include mobile {
		left: 10px; bottom: 30px;
	}
 }
 
 .info {
	// position: fixed; /* Фиксированное положение */
	left: 0; bottom: 55px; /* Левый нижний угол */
	width: 100%; /* Ширина слоя */
	text-align: center;
	justify-content: space-between;
	display: flex;
	margin-bottom: 10px;

	@include mobile {
		margin-bottom: 0;
	}
 }
 .col {
	width: 30%;
	font-family: 'Oswald', sans-serif;
 }
 #first_col{
	text-transform: uppercase;
	font-size: 22px;

	@include mobile {
		font-size: 14px;
	}
 }
 #second_col{
	margin-top: 12px;
	font-family: 'Montserrat', sans-serif;
	font-size: 15px;
 }
 #second_col:hover{
	text-decoration: underline;
 }

 #social_item {
	margin: 0 7px;
	}
	a{
	  text-decoration: none;
	  color: -webkit-link;
	  cursor: pointer;
	  color: #fff;
	}
	i {
	  font-size: 25px;
	}
	
	#copyright{
	//   position: fixed; /* Фиксированное положение */
	  left: 0; bottom: 0; /* Левый нижний угол */
	  padding: 10px; /* Поля вокруг текста */
	  width: 100%; /* Ширина слоя */
	  text-align: center;
	  border-top: 1px solid #e5e5e50d;
	  color: #a99c9c;
	  font-size: 15px;
	  font-weight: 200;
	  font-family: 'Oswald', sans-serif;
	}
	#copyright span {
	color: #ffffffb0;
	font-weight: 400;
	font-family: 'Oswald', sans-serif;
	}

	@include mobile {
	.footer{
		left: 10px; bottom: 30px;
		color:#fff;
	 }
	 
	 .info {
		left: 0; bottom: 40px; /* Левый нижний угол */
	 }
	 .col {
		width: 30%;
		font-family: 'Oswald', sans-serif;
	 }
	 #first_col{
		font-size: 14px;
	 }
	 #second_col{
		margin-top: 5px;
		margin-right: 10px;
		font-size: 10px;
	 }
	 /* Social */
	 #social_item {
	 margin: 0 4px;
	 margin-bottom: 10px;
	 }
	 i {
		font-size: 23px;
	 }
	 #copyright{
		padding: 7px; /* Поля вокруг текста */
		font-size: 12px;
	 }
  }
	

