@import "../mixins";

.catalog {
	padding: var(--big-offset) 0;

	&__left {
		grid-column: 3 span;

		@include tablet {
			display: none;
		}
	}

	&__grid {
		display: flex;	
		flex-direction: column;
		align-items: center;
		grid-column: 9 span;

		
		@include tablet {
			grid-column: 12 span;
		}
		
	}

	&__reset {
		transition: color .3s ease-in-out;

		&:focus {
				color: var(--color-eastbay);
			}

		@include desktop {
			&:hover {
				color: var(--color-eastbay);
			}
		}
	}
	
	&__subtitle {
		margin: 0;
		font: var(--title-font);
		margin-bottom: var(--second-offset);
	}

	&__caption {
		display: block;
		margin-bottom: var(--small-offset);
		font: var(--middle-font);
		color: var(--color-main);

		
	}

	&__prop {
		margin-bottom: var(--second-offset);
	}
	&__prop-checkboxes {
		display: flex;
		justify-content: space-between;
	}

	&__apply {
		margin-bottom: var(--second-offset);
		width: 100%;
	}

	&__more {
		margin-top: var(--big-offset);
	}
}

.catalog-list {
	grid-template-columns: repeat(9, 1fr);
	&__item {
		grid-column: 3 span;
	}

	@include mobile {
		grid-template-columns: repeat(4, 1fr);
		&__item {
			grid-column: 4 span;
		}
	}
}

.catalog-filters {
	border-radius: 4px;
	background-color: var(--color-bg);
	border-radius: 4px;
	padding: var(--second-offset);

	&__btns {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.catalog-price {

	&__wrapper {
		position: relative;
		display: flex;
		flex-wrap: wrap;	

		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 8px;
			transform: translateX(-50%);
			width: 1px;
			height: 34px;
			bottom: 8px;
			background-color: var(--color-gray);
		}
	}

	&__input {
		border: 1px solid var(--color-gray);
		width: 50%;
		height: 50px;
		text-align: center;
		font: var(--middle-font);
		color: var(--color-main);
		background-color: transparent;

		&:first-child {
			border-right: none;
			border-top-left-radius: 4px;
			border-bottom-left-radius: 4px;
	}
		&:last-child {
			border-left: none;
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
	}
	}
	.noUi-horizontal {
		height: 2px;
	}
	.noUi-target {
		border: none;
		box-shadow: none;
		background-color: var(--color-main);
	}
	.noUi-connect {
		background: var(--color-main);
	}

	.noUi-horizontal .noUi-handle {
		top: -8px;
		width: 5px;
		height: 18px;
	}

	.noUi-handle {
		right: 0;
		border: none;
		border-radius: 100px;
		box-shadow: none;
		background: var(--color-main);
		cursor: pointer;
  }

  .noUi-handle::after,
  .noUi-handle::before {
	  display: none;
  }

	.noUi-handle-lower {
		right: -3px;
	}
}