@import "../mixins";

.instagram {
	padding: var(--big-offset);

	&__container {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		@include small-tablet {
			justify-content: center;
		}
	}

	&__left {
		flex-shrink: 0;
		width: 380px;
		margin-right: var(--small-offset);
		border-radius: 4px;
		background-color: var(--color-eastbay);

		@include mobile {
			margin-right: 0;
			width: 100%;
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: calc(100% - (380px + var(--small-offset)));
		@include small-tablet {
		display: none;
		}
	}

	&__title {
		margin-bottom: var(--second-offset);
		color: var(--color-white);
	}

	&__descr {
		margin: 0 auto;
		text-align: center;
		margin-bottom: var(--main-offset);
		max-width: 260px;
		color: var(--color-white);
		font: var(--middle-font);
	}
	&__input {
		margin-bottom: var(--small-offset);
	}
	
	&__btn {
		width: 100%;
	}

	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 160px);
		gap: 15px;
	}

	&__item {
		a{
			display: block;
			width: 100%;
			height: 100%;
		}

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&:nth-child(1) {
			grid-column: 2 span;
			grid-row: 2 span;
		}

		&:nth-child(2) {
			grid-row: 1;
		}
	}

	@include mobile {
		padding: 40px 0;
	}

.callback-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: var(--main-offset) var(--second-offset);

}

}