@import "../mixins";

.modal {
@include tablet {
&::before {
	display: none;
}
}
}

.prod-modal {
	border-radius: 4px;
	padding: var(--main-offset);
	width: 1140px;

	.modal__close {
		display: none;

		@include tablet {
			z-index: 500;
			display: block;
		}
	}

	&__top {
		display: flex;
		justify-content: space-between;

		@include small-tablet {
			flex-direction: column;
		}
	}

	&__bottom {
		margin-bottom: var(--main-offset);
	}

	&__video {
		width: 100%;
		height: 450px;
		iframe {
			display: block;
			border: none;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	@include tablet {
		margin: 0;
		width: 100%;
	}

}

.modal-slider {
	margin-right: var(--second-offset);
	width: calc(50% - (var(--second-offset) / 2));

	&__container {
		overflow: hidden;
		margin-bottom: var(--second-offset);
		width: 100%;
		font-size: 0;
		img {
			width: 100%;
			object-fit: cover;
			max-height: 460px;
		}
	}
	@include small-tablet {
		width: 100%;
		margin-bottom: 30px;
		margin-right: 0;
	}
}

.modal-info {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-right: var(--big-offset);
	width: calc(50% - (var(--second-offset) / 2));

	&__top {
	display: flex;
	align-items: center;
	margin-bottom: var(--main-offset);
	font: var(--middle-font);
	color: var(--color-gray);
	margin-bottom: var(--main-offset);
	}

	&__vendor {
		margin-right: var(--main-offset);

		@include tablet {
			margin-right: 10px;
		}
	}

	&__quantity {
		span {
			color: var(--color-main);
		}
	}

	&__title {
		margin: 0;
		margin-bottom: var(--second-offset);
		font: var(--title-font);
		line-height: 140%;
		color: var(--color-main);
	}

	&__rate {
		margin-bottom: var(--second-offset);
	}

	&__subtitle {
		display: inline-block;
		margin-bottom: var(--second-offset);
		font: var(--middle-font);
		line-height: 16px;
		color: var(--color-main);
	}

	&__price {
		display: flex;
		align-items: center;
	}

	&__current-price {
		margin-right: 15px;
		font: var(--middle-high-font);
		line-height: 30px;
		color: var(--color-main);
	}

	&__old-price {
		text-decoration: line-through;
		font: var(--middle-font);
		color: var(--color-gray);
	}

	&__order {
		margin-top: auto;
		width: 100%; 
		
		@include small-tablet {
			margin-top: 30px;
		}
	}

	@include small-tablet {
		margin: 0;
		width: 100%;
	}
}

.modal-sizes {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&__item {
		margin-bottom: var(--small-offset);
		&:not(:last-child) {
			margin-right: var(--small-offset);
		}
	}

	&__btn {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border: 1px solid var(--color-gray);
		border-radius: 4px;
		padding: 6px 0;
		width: 70px;
		font: var(--small-font);
		color: var(--color-main);
		transition: border .3s ease-in-out;

		@include desktop {
		&:hover {
			border: 1px solid rgb(117, 117, 117);
			box-shadow: 0 0 3px rgba(97, 97, 97, 0.5);
		}
	}

		&:active {
			border: 1px solid var(--color-eastbay);
			box-shadow: 0 0 3px rgba(97, 97, 97, 0.5);
		}
	}

}

.modal-preview {
	display: grid;
	grid-template-columns: repeat(6, 70px);
	gap: var(--second-offset);

	&__item {
		border-radius: 4px;
		height: 75px;
		transition: opacity .3s;
		cursor: pointer;


		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		@include desktop {
		&:hover {
			opacity: .7;
		}
	}
		&--active {
			opacity: .7;
			pointer-events: none;
		}
	}
}

.modal-note {
	margin-top: var(--second-offset);
	&__item {
		padding-left: 25px;
		font: var(--small-font);
		color: var(--color-gray);
		background-image: url('../img/checklist.svg');
		background-position: left center;
		background-size: 15px 12px;
		background-repeat: no-repeat;

	&:not(:last-child) {
		margin-bottom: var(--small-offset);
	}
}
}

.prod-bottom {
	display: flex;
	margin-top: var(--main-offset);

	&__column {
		width: calc(50% - (var(--second-offset) / 2));

		&:first-child {
			margin-right: var(--second-offset);
		}
		@include small-tablet {
			width: 100%;
			&:first-child {
				margin-right: 0;
				margin-bottom: 30px;
			}

		}
	}

	&__title {
		margin: 0;
		margin-bottom: var(--second-offset);
		font: var(--title-font);
		color: var(--color-main);
	}	

	&__descr {
		margin: 0;
		max-width: 430px;
		font: var(--middle-font);
		color: var(--color-main);
	}

	@include small-tablet {
	display: block;
	}
}