@import "../mixins";

.hero {
position: relative;
height: 528px;
background-color: var(--color-eastbay);
padding: 130px 0;


&__container {
	position: relative;
	z-index: 5;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
}

&__bg-text {
	position: absolute;
	left: 50%;
	top: 50%;
	z-index: 0;
	margin-top: 20px;
	margin-left: 50px;
	font-family: 'Intro', sans-serif;
	color: rgba(255, 255, 255, 0.12);
	font-weight: 700;
	font-size: 200px;
	line-height: 200px;
	transform:translate(-50%, -50%);

	@include tablet {
		display: none;
	}
}

&__title {
	margin: 0;
	max-width: 50%;
	margin-bottom: var(--second-offset);
	font: 700 36px / 140% var(--font-family);
	color: var(--color-white);

	@include small-tablet {
		margin-top: 0;
		max-width: none;
		font-size: 30px;
	}
}

&__descr {
margin: 0;
margin-bottom: var(--main-offset);
font: var(--middle-font);
max-width: calc(50% - 100px);
color: var(--color-white);

@include small-tablet {
	max-width: none;
}
}

@include small-tablet {
	padding: 80px 0 20px 0;
	height: auto;
}
}