.cart-modal {
	border-radius: 4px;
	padding: var(--main-offset);
	width: 580px;

	.modal__close {
		display: none;

		@include small-tablet {
			z-index: 500;
			display: block;
		}
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: var(--main-offset);

		@include small-tablet {
			display: block;
		}
	}

	&__title {
		margin: 0;
		font: var(--price-font);
		color: var(--color-main);
	}
	
	&__order-number {
		font: var(--small-font);
		line-height: 14px;
		color: var(--color-gray);
	}

	&__input {
		color: var(--color-gray);
		background-color: #f6f6f6;
		&:not(:last-child) {
			margin-bottom: var(--small-offset);
		}
	}

	&__btn {
		margin-top: 30px;
	}

	@include small-tablet {
		width: 100%;
		max-width: 570px;
	}
}

.cart-modal-order {
	margin-bottom: var(--main-offset);
	border: 1px solid #d9d9d9;
	border-radius: 4px;
	padding: 30px;

	&__top {
		font: var( --middle-font);
		line-height: 16px;
		color: var(--color-gray);

		@include mobile {
			font-size: 14px;
		}
	}

	&__main {
		color: var(--color-main);
		font-weight: 700;
	}

	&__list {
		display: none;

		&--visible {
			display: block;
		}
	}

	&__quantity {
		margin-bottom: var(--second-offset);
	}

	&__summ {
		margin-bottom: var(--second-offset);
	}

	&__show {
		display: inline-flex;
		align-items: center;
		margin-bottom: var(--second-offset);
		color: var(--color-main);
		svg {
			margin-left: 5px;
			width: 8px;
			height: 8px;
			fill: currentColor;
			object-fit: cover;
			transition: transform .3s ease-in-out;
		}

		&--active {
			svg {
				transform: rotate(180deg);
			}
		}
	}

	.mini-cart__item {
		&:not(:last-child) {
			margin-bottom: 14px;
		}
	}

}
