@import "../mixins";

.quiz {
	padding: var(--big-offset) 0;

	&__content {
		padding: var(--main-offset) 100px;
		background-color: var(--color-bg);
		border-radius: 4px;

		@include small-tablet {
			padding: var(--main-offset);
		}
	}

	&__title {
		margin-bottom: var(--second-offset);
	}

	&__descr {
		margin: 0;
		border-bottom: 1px solid currentColor;
		font: var(--middle-font);
		padding-bottom: var(--small-offset);
		color: var(--color-line)

	}
}

.quiz-questions {
	padding: var(--second-offset) 0;
}

.quiz-question {
	&__title {
		font: var(--title-font);
		color: var(--color-main);
		margin: 0;
		margin-bottom: var(--second-offset);
	}

	&__answers {
		display: grid;
		grid-auto-rows: 155px;
		grid-template-columns: repeat(3, 1fr);
		gap: var(--second-offset) 70px;
		padding-bottom: var(--main-offset);

		@include small-tablet {
			gap: var(--second-offset);
			grid-auto-rows: 90px;
		}
		// @include mobile {
		// 	display: block;
		// }
	}

	&__item {
		img {
			margin-bottom: var(--small-offset);
		}

		@include mobile {
			&:not(:last-child) {
				margin: 0;
			}
		}
	}

	&__count {
		color: var(--color-line);
		font: var(--middle-font)
	}
	
	&__message {
		border: none;
		border-radius: 4px;
		padding: var(--second-offset);
		width: 465px;
		height: 246px;
		color: var(--color-cameo);
		background: var(--color-white);
		resize: none;

		@include mobile {
			width: 100%;
		}
	}
}


.quiz-bottom {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border-top: 1px solid var(--color-line);
	padding-top: var(--second-offset);
}

.last-question {
	display: none;
	padding-top: var(--second-offset);
	&__form {
		position: relative;
		border-radius: 4px;
		padding: var(--main-offset);
		background: var(--color-cameo);
		max-width: 655px;

	
	}

	&__decorate {
		position: absolute;
		top: -5px;
		right: -185px;

		@include small-tablet {
			display: none;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: var(--small-offset);
		font-weight: bold;
		font-size: 36px;
		line-height: 140%;
		color: var(--color-white);

		@include mobile {
			font-size: 24px;
			line-height: 30px;
		}
	}

	&__descr {
		margin: 0;
		margin-bottom: var(--second-offset);
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 140%;
		color: var(--color-white);
	}

	&__input {
		max-width: 500px;
		margin-bottom: var(--small-offset);

		&:nth-of-type(2) {
			margin-bottom: var(--second-offset);
		}

		@include mobile {
			max-width: none;
		}

	}

	&__btn {
		min-width: 220px;

		@include mobile {
			width: 100%;
			min-width: auto;
		}
	}
}