.about {
position: relative;
overflow: hidden;
background-color: var(--color-eastbay);
height: 580px;

	&__container {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;
	}

	&__figure {
		position: absolute;
		left: 0;
		top: 0;
	}

	&__title {
		margin-bottom: var(--second-offset);
		color: var(--color-white);
		font-size: 40px;
		@include mobile {
			font-size: 35px;
		}
	}

	&__descr {
		font: var(--nav-font);
		color: var(--color-white);
		font-size: 18px;
	}

	&__quote {
		font: inherit;
		display: inline-block;
		margin-bottom: var(--second-offset);

		&::before, &::after {
			display: none;
		}
	}

	&__author {
		align-items: center;
		display: flex;
		justify-content: flex-end;
		font: var(--price-font);
		font-style: normal;
		transform: translateX(80px);
		font-size: 25px;
		
		@include small-tablet {
			transform: translateX(20px);
		}
		@include mobile {
			transform: translatey(40px);
		}


	&::before {
		content: "";
		display: inline-block;
		margin-right: var(--second-offset);
		width: 30px;
		height: 3px;
		background-color: var(--color-white);
	}
	}

	&__text{
		align-items: center;
		justify-content: center;
		width: 45%;
		// transform: translatey(60px);

		@include small-tablet {
			width: 80%;
		}
	}
	

