@import "../mixins";

.product {
	overflow: hidden;
	border-radius: 4px;

	&__btns {
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		background: rgba(255, 255, 255, 0.6);
		transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
		opacity: 0;
		// visibility: hidden;

		@include mobile {
			opacity: 1;
		}
	}

	&__image {
		position: relative;
		width: 100%;
		max-height: 293px;
		margin-bottom: var(--small-offset);
		
		img {
			width: 100%;
			height: 293px;
			display: block;
			object-fit: cover;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: var(--small-offset);
		font: var(--middle-font);
		color: var(--color-main);
	}

	&__price {
		font: var(--price-font);
		color:var(--color-main);
	}

	&__btns {
		&--active {
			opacity: 1;
			
		}
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		width: 80px;
		height: 80px;
		background-color: var(--color-main);
		transition: background-color .3s ease-in-out;
		svg {
			width: 30px;
			height: 30px;
			object-fit: cover;
			fill: var(--color-white);
		}
		&:first-child {
			margin-right: var(--main-offset);
		}

		&:focus{
			background-color: var(--color-main-hover);
			}

		@include desktop {
		&:hover{
		background-color: var(--color-main-hover);
		}
	}

	&:active{
		background-color: var(--color-eastbay);
		}

	
	&--disabled {
		opacity: 0.4;
		pointer-events: none;
	}
	}
	@include desktop {
	&:hover {
		.product__btns {
			opacity: 1;
			visibility: visible;
		}
	}
	}
	
}


.mini-product {
	display: flex;
	align-items: center;
	padding: var(--small-offset) var(--second-offset);

	&__image {
		flex-shrink: 0;
		margin-right: var(--small-offset);
		width: 100px;
		height: 100px;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@include mobile {
			width: 60px;
			height: 60px;
		}
	}

	&__text {
		margin-right: auto;
		max-width: 268px;
	}

	&__title {
	margin: 0;		
	font: var(--small-font);
	line-height: 20px;
	margin-bottom: var(--small-offset);
	color: var(--color-main);
	}

	&__delete {
		flex-shrink: 0;
		width: 20px;
		height: 20px;
		font-size: 0;
		svg {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			pointer-events: none;
		}
	}

	&__content {
		flex-grow: 1;
		display: flex;
		align-items: center;
	}

	&__price {
		font: var(--price-font);
		color: #4d4d4d;
	}
}

.cart-modal {

	.mini-product {
		position: relative;
		display: flex;
		align-items: center;
		padding: 0;
	
		&__image {
			flex-shrink: 0;
			margin-right: var(--small-offset);
			width: 80px;
			height: 45px;
	
			img {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	
		&__text {
			margin-right: auto;
			max-width: 290px;
		}
	
		&__title {
		margin: 0;		
		font: var(--small-font);
		line-height: 20px;
		margin-bottom: var(--small-offset);
		color: var(--color-main);
		}
	
		&__delete {
			width: auto;
			height: auto;
			font: var(--small-font);
			line-height: 14px;
			color: var(--color-gray);

			svg {
				display: none;
			}
		}
	
		&__content {
			flex-grow: 1;
			display: flex;
			align-items: center;
		}
	
		&__price {
			font: var(--price-font);
			color: #4d4d4d;
		}
		@include mobile {
			display: block;
			
		}
	}
}

