@import "../mixins";

.team {
	position: relative;
	padding: var(--big-offset);
	background-color: var(--color-eastbay);
	@include small-tablet {
		padding: var(--second-offset);
	}

	&__title {
		color: var(--color-white);
		margin-bottom: var(--main-offset);
	}

	&__list {
		position: relative;
		
		gap: var(--main-offset) var(--second-offset);
	}

	&__figure {
		position: absolute;
		right: 0;
		top: 0;

		@include small-tablet {
			display: none;
		}
	}

	&__item {
		grid-column: 4 span;
		z-index: 5;

		@include mobile {
			grid-column: 5 span;
		}
	}

	&__img {
		overflow: hidden;
		border-radius: 4px;
		margin-bottom: var(--second-offset);
	
		@include small-tablet {
			width: 100%;
		}
	}

	&__subtitle {
		display: block;
		color: var(--color-white);
		font: var(--team-font);
		margin-bottom: var(--small-offset);

		@include small-tablet {
			font-size: 18px;
		}
	}
	&__post {
		font : var(--middle-font);
		color: var(--color-white);

		@include small-tablet {
			font-size: 14px;
			margin-bottom: var(--second-offset);
		}
	}
}