@import "../mixins";

.faq {
	padding: var(--big-offset);

	&__title {
		margin-bottom: var(--main-offset);
	}

	&__list { 
		margin: 0 auto;
		max-width: 880px;

	}

	@include mobile {
		padding: 35px 0;
	}
}

.faq-accordion {

	border-top: 1px solid var(--color-faq);
	border-bottom: 1px solid var(--color-faq);


	&__control {
		padding: 30px 0;
		width: 100%;
		text-align: left;
		font: var(--price-font);
		color: var(--color-faq);
		background-color: transparent;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__content {
		overflow: hidden;
		padding-right: 70px;
		padding-bottom: 0;
		max-height: 0;
		padding-bottom: 0;
		box-sizing: content-box;
		opacity: 0;
		transition: all 0.3s ease-out;
		will-change: max-height;
		font: var(--middle-font);
		color: #24242494;
	}

	&__icon {
		display: inline-block;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		transition: transform 0.3s ease-out;
		width: 25px;
		height: 25px;
		background-image: url("../img/cross.svg");

		@include mobile {
			width: 20px;
			height: 20px;
		}
	}

	&:not(:first-child) {
		margin-top: -1px;
	}
	
}
.open .faq-accordion__icon {
	transform: rotate(45deg);
}

.open .faq-accordion__content {
	padding-bottom: 18px;
	opacity: 1;
}
.sizes__table {
	text-decoration: none;
	color: var(--color-eastbay);
}