@import "../mixins";

.contacts {
	padding: var(--big-offset) 0;
	background-color: #f3f6f6;

	&__container {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;

		@include tablet {
			flex-wrap: wrap;
		}
	}

	&__title {
		display: flex;
		align-items: center;
		margin-bottom: var(--big-offset);
	}

	&__item {
	font-style: normal;
	margin-bottom: var(--main-offset);
	}

	&__subtitle {
		margin: 0;
		margin-bottom: var(--main-offset);
		font: var(--small-font);
		text-transform: uppercase;
		color: var(--color-gray);
	}


	&__phone {
		display: inline-block;
		outline: none;
		margin-bottom: var(--small-offset);
		font: var(--middle-high-font);
		color: var(--color-main);
		transition: color .3s ease-in-out;

		&:focus {
			color: var(--color-eastbay);
		}

		@include desktop {
			&:hover {
				color: var(--color-eastbay);
			}
		}
	}
	
	&__text {
		margin-right: var(--second-offset);
		width: calc(100% - (680px + var(--second-offset)));

		@include tablet {
			margin-right: 0;
			width: 100%;
			margin-bottom: var(--second-offset);
		}
	}

	&__address {
		display: block;
	}

	&__map {
		width: 680px;
		height: 500px;

		// @include tablet {
		// 	width: 100%;
		// }
		// @include mobile {
		// 	width: 30%;
		// }
	}
}

.contacts__map {
	width: 680px;
	height: 500px;


	@include small-tablet {
			width: 100%;
		}

	@include mobile {
		height: 350px;
	}
}